export function aliLogin(callback) {
	// 阿里百川聊天登陆接口
	const uid = window.localStorage.getItem('uid')
	const appkey = window.localStorage.getItem('appkey')
	const credential = window.localStorage.getItem('credential')
	var sdk = window.sdk || (window.sdk = new WSDK())

	sdk.Base.login({
		uid: uid,
		appkey: appkey,
		credential: credential,
		timeout: 5000,
		success: function (data) {
			// {code: 1000, resultText: 'SUCCESS'}
			console.log('login success', data)
			window.aliLogined = true
			console.log(window.aliLogined)
		},
		error: function (error) {
			// {code: 1002, resultText: 'TIMEOUT'}
			console.log('login fail', error)
		}
	})
}



