	import axios from 'axios'
	import TopHeader from 'components/common/topHeader'
	import Search from 'components/common/search'
	import { aliLogin } from '../chat/api/aliLogin'
	import grapRed from 'components/chat/red/red'

	export default {
		data() {
			const uid = window.localStorage.getItem('uid')
			const userName = window.localStorage.getItem("userName")
			const myId = window.localStorage.getItem("myId")
			const chatId = window.localStorage.getItem("chatId")
			const friendTouid = window.localStorage.getItem("friendTouid")
			const friendNick = window.localStorage.getItem("friendNick")
			return {
				userName,
				msgsCont: '',
				msgsNum: Number,
				uid,
				hasMessages: false,
				myId,
				list: [],
				friendIds: [],
				chatId,
				friendTouid,
				chatName: '',
				hasHead: false,
				headUrl: '',
				hasPic: false,
				friendNick
			}
		},
		created() {
			if(this.chatId !== null) {
				axios
					.get("http://121.42.177.97:8080/red/use/pic_id.do?user_id=" + this.chatId)
					.then(res => {
						console.log('有图')
						this.hasPic = true
					})
					.catch(res => {
						throw res
						console.log('无图')
					})
			}
			axios
				.post("http://121.42.177.97:8080/red/user/allfriend.do?name=" + this.userName)
				.then(res => {
					console.log(res.data.data)
				 	this.list = res.data.data
					for(var val of res.data.data) {
						this.friendIds.push(val.friend_id.toString())
					}
					console.log('好友ID：' + this.friendIds)
					this.getChatName()
					// this.allHistory()
				})
				.catch(res => {
					throw res
				})
		},
		mounted() {
			//			location.replace();
			//			this.reurl()
			//   location.reload();

		},
		components: {
			TopHeader,
			Search,
			grapRed
		},
		methods: {
			/* reurl() {

			   var	 url = location.href; //把当前页面的地址赋给变量 url
                 alert(url)
				var times = url.split("?"); //分切变量 url 分隔符号为 "?"

				if(times[1] != 1) { //如果?后的值不等于1表示没有刷新

					url += "?1"; //把变量 url 的值加入 ?1

					location.reload(url); //刷新页面

				}
			}, */
			allHistory() {
				if(window.aliLogined) {
					console.log('阿里登录成功！')
					aliFn()
				} else {
					// console.log('阿里未登录')
					aliLogin(aliFn)
				}
				var that = this

				function aliFn() {
					that.getHistory()
					that.noReadMsg()
				}
			},
			getHistory() {
				var that = this
				var nextkey = ''
				var friendTouid = that.friendTouid
				sdk.Chat.getHistory({
					touid: friendTouid,
					nextkey,
					count: 10,
					success: function(data) {
						nextkey = data.data && data.data.next_key;
						var hasMessages = that.hasMessages;
						if(data.data.length !== 0) {
							hasMessages = true
							that.msgsCont = data.data.msgs[0].msg
							console.log(that.msgsCont)
						} else {
							console.log('没有消息')
						}
					},
					error: function(error) {
						console.log('get history msg fail', error);
					}
				})
			},
			noReadMsg() {
				var that = this
				// 未读消息数
				sdk.Base.getUnreadMsgCount({
					count: 10,
					success: function(data) {
						console.log('get unread msg count success', data)
						var msgsNum = that.msgsNum
						that.msgsNum = data.data.length
					},
					error: function(error) {
						console.log('get unread msg count fail', error)
					}
				})
			},
			getChatName() {
				if(this.chatId !== null) {
					axios
						.get("http://121.42.177.97:8080/red/user/friend_nick.do?user_id=" + this.myId + '&' + 'friend_id=' + this.chatId)
						.then(res => {
							this.chatName = res.data.data[0].friend_nick
						})
						.catch(res => {
							throw res
						})
				}
			}
		}
	}



